.fontItem{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
}

.fontIcon{
    margin-right: 10px;
}

.fontIcon{
    cursor: pointer;
}

.fontIcon:hover{
    color:red;
}

.fontItem:not(:last-child){
    border-bottom: 1px solid #ccc;
}

