.annotationToolbar{
    z-index: 10000000000;
}

.annotationDiv .annotationToolbar{
    display: none;
}

.annotationDiv.selected .annotationToolbar{
    display: inline-flex;
}

.annotationToolbar .aicon{
    width: 20px;
    height: 20px;
    background-color: black;
    border-radius: 3px;
    color:white;
    display: flex;
    align-content: center;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: background-color 200ms ease-in-out;
}


.annotationToolbar .aicon:hover{
    background-color: red;
}