.rnAResizeItem{
    z-index: 10000000000;
    width: 6px;
    height: 6px;
    background-color: black;
    position: absolute;
    display: none;
    transition: background-color 200ms ease-in-out;
}

.rnAResizeItem:hover{
    background-color: red;
}

.annotationDiv.selected .rnAResizeItem{
    display: block;
}