.SlateField{
    user-select:none;
    cursor:pointer;
    background-color:#f9f9f9;
    padding:0px 2px;
    border:1px solid #dfdfdf;
    max-width:170px;
    overflow:hidden;
    white-space:nowrap;
    text-overflow:ellipsis;
    margin-right: 3px;
    margin-left: 3px;
}

.SlateField.rnFieldSelected{
    background-color: #4d80e4;
    color:white;
    border-color: #679AFE;
}

