.rnTagIcon{
    color:#ccc;
    transition: color 200ms ease-in-out;
}

.rnTagIcon:hover{
    color:blue;
}


.fieldPickerTextBox{
    min-height: 33.5px;
    border:1px solid #dfdfdf;
    border-radius: 4px;
    padding:6px 12px;
    padding-right: 30px;
}

.fieldPickerTextBox:focus{
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6);
    border-color: #66afe9;
    outline: 0;
}

.rbt-token {
    text-overflow: ellipsis;
    max-width: 170px;
    overflow: hidden;
    white-space: nowrap;
}